.container {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;

    @media screen and (min-width: 320px) {
        width: 290px;
    }

    @media screen and (min-width: 768px) {
        width: 720px;
    }

    @media screen and (min-width: 1200px) {
        width: 1250px;
    }
}
